$bgcolor : rgb(217,217,150);
$textcolor: red;
$fontsize : 18px;


// 주석종류1
/* 주석종류2 */

.list1{
    background-color: $bgcolor;
    color: $textcolor;
    font-size: $fontsize;
}

.list2{
    ul{
        margin:0;
        padding:0;
        list-style:none;
    }
    li{
        display:inline-block;
    }
    span{
        display:block;
        padding:6px 12px;
        text-decoration:underline;
        font-size:$fontsize;
        color:$textcolor;
    }
}

.list3{
    width : 400px;
    border : 1px solid red;
    font : {
        family : Arial, sans-serif;
        size : 30px;
        weight : bold;
    }
    text : {
        align : center;
        transform : uppercase;
        decoration : line-through;
    }
}

/*

@mixin name {
    property value; ....
}
적용 : selector { @include mixin-name }

*/
@mixin list {
    color : red;
    font-size: 25px;
    font-weight : bold;
    border : 1px solid blue;
}

.list4{
    @include list;
    background-color:yellow;
}

.list5{
    @include list;
    background-color:pink;
}

@mixin bordered ($color, $width){
    border : $width dashed $color;
}
.list6{
    @include bordered(blue, 2px)
}
.list7{
    @include bordered(red, 3px)
}

.btn{
    border:none;
    outline:none;
    width:100px;
    height:30px;
    font-size:20px;
    text-align:center;
    cursor:pointer;
}

.list8{ // extend : 상속
    @extend .btn ;
    background-color:orange;
}

.list9{ // extend : 상속
    @extend .btn ;
    background-color:green;
    color:white;
}