.App {
  margin:20px;
}

.txt2{
  color:red;
  font-size:20px;
}

.show_menu{
  display:inline-block;
}
.hide_menu{
  display:none;
}

.active{
  color:red;
}

#bar1, #bar2, #bar3{
  width:35px;
  height:5px;
  background-color:#333333;
  font-size:0;
  margin:6px 0;
  transition:0.4s;
}

.bar1{
  transform:translate(0,11px) rotate(-45deg);
}
.bar2{
  opacity:0;
}
.bar3{
  transform:translate(0,-11px) rotate(45deg);
}


.Link a{
  color:black;
  margin-right:5px;
  text-decoration:none;
}
.Link a:hover{
  color:red;
  text-decoration:underline;
}